.payment {
    &__heading {
        margin: 30px 0 19px 0;
        color: #515151;
        font-size: 30px;
        font-weight: 400;
        letter-spacing: 0.6px;
        text-align: center;

        @media (max-width: 767px) {
            display: none;
        }
    }

    &__row {
        @include row;
        position: relative;
        padding-top: 50px;
        padding-bottom: 50px;
        border-top: 1px solid #d6d6d6;
        border-bottom: 1px solid #d6d6d6;

        &::before {
            content: '';
            position: absolute;
            left: 50%;
            top: 50px;
            bottom: 50px;
            width: 1px;
            background: #d6d6d6;
        }

        @media (max-width: 767px) {
            padding-top: 21px;
            padding-bottom: 21px;

            &::before {
                display: none;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            padding-top: 30px;
            padding-bottom: 30px;
        }
    }

    &__column {
        @include width(50%);

        &:first-child {
            padding-right: 49px;
        }
        
        &:last-child {
            padding-left: 49px;
        }

        @media (max-width: 767px) {
            @include width(100%);

            &:first-child {
                padding-right: 0;
            }

            &:last-child {
                display: none;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            &:first-child {
                padding-right: 28px;
            }
            
            &:last-child {
                padding-left: 28px;
            }
        }
    }
    
    &__available {
        display: block;
        margin-bottom: 25px;
        color: #515151;
        font-size: 20px;

        @media (min-width: 768px) and (max-width: 1024px) {
            margin-bottom: 17px;
            font-size: 16px;
        }
    }

    &__description {
        color: #515151;
        font-size: 16px;

        @media (min-width: 768px) and (max-width: 1024px) {
            font-size: 13px;
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        padding: 0 48px;
    }
}