.telegram {
    display: block;
    width: 26px;
    height: 26px;  
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url('~@/assets/images/icons/telegram.png');

    @media (min-width: 768px) and (max-width: 1024px) {
        width: 20px;
        height: 20px;
    }
}