.mobile-breadcrumbs {
  display: none;
  padding: 9px 0 9px 20px;
  margin-top: 14px;
  background-color: #8fb35b;

  &__link {
    color: #fff;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 0.28px;

    span {
      font-weight: 400;
    }

    i {
      font-size: 13px;
      font-weight: 600;
    }
  }

  & + .seo-block {
    display: none;
  }

  @media (max-width: 767px) {
    position: relative;
    display: flex;
    justify-content: center;
    margin: 0 -9px;

    & + .seo-block {
      display: block;
    }

    i {
      position: absolute;
      top: calc(50% - 13px/2);
      left: 9px;
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    display: block;
  }
}