.category-list__item-button {
  padding: 8.5px 18.5px;
  border: 1px solid #8fb35b;
  background: none;
  border-radius: 3px;
  color: #515151;
  font-size: 14px;
  cursor: pointer; }
  .category-list__item-button:hover {
    color: #fff;
    background-color: #8fb35b; }
  @media (max-width: 767px) {
    .category-list__item-button {
      padding: 5.5px 18.5px;
      margin: auto;
      font-size: 11px; } }
  @media (min-width: 768px) and (max-width: 1024px) {
    .category-list__item-button {
      padding: 6px 17px;
      flex-shrink: 0; } }

.product-form__buttons .category-list__item-button {
  padding: 12.5px 26px;
  margin: 0;
  color: #fff;
  background-color: #8fb35b; }
  .product-form__buttons .category-list__item-button:hover {
    background-color: #fbc000; }
  @media (max-width: 767px) {
    .product-form__buttons .category-list__item-button {
      font-size: 14px; } }
