.cart {
  position: relative;
  padding: 14.5px 0;
  border-radius: 3px;
  color: #fff;
  font-weight: 700;
  background-color: #8fb35b;
  text-align: center;
  cursor: pointer; }
  .cart:hover {
    background-color: #fbc000; }
  .cart .icon-cart {
    margin-right: 10px; }
  .cart .icon-shopping-cart {
    font-size: 18px; }
  .cart_mobile {
    flex-basis: auto;
    width: auto;
    padding: 0;
    background: transparent;
    color: #000;
    margin-left: 10px; }
    @media (max-width: 767px) {
      .cart_mobile {
