.category-preview {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 39px;
  margin-top: 40px;

  &__content {
    position: relative;
    width: calc(100% - 29.3% - 47px);
    flex-basis: calc(100% - 29.3% - 47px);

    @media (min-width: 768px) and (max-width: 1024px) {
      width: calc(100% - 25.1% - 35px);
      flex-basis: calc(100% - 25.1% - 35px);
    }
  }

  &__aside {
    @include width(29.3%);

    @media (min-width: 768px) and (max-width: 1024px) {
      @include width(25.1%);
    }
  }

  @media (max-width: 767px) {
    display: none;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    margin-top: 52px;
    margin-bottom: 173px;
  }
}