.specials {
    position: relative;
    display: inline-block;
    margin-bottom: 24px;
    z-index: 50;
    color: #404040;
    font-size: 20px;
    line-height: 20px;
    border-bottom: 1px solid #404040;

    &:hover {
        color: #7e9d51;
        border-bottom: 1px solid #7e9d51;
    }

    @media (max-width: 767px) {
        margin-bottom: 8px;
        font-size: 14px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        margin-bottom: 29px;
        font-size: 16px;
    }
}