.product {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;

  &__wrapper {
    width: 57.28%;

    @media (max-width: 767px) {
      @include width(100%);

      .action {
        display: none;
      }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      @include width(56%);

      .action {
        display: none;
      }
    }
  }

  &__image {
    position: relative;
    align-self: flex-start;

    .glide__bullets {
      position: absolute;
      bottom: 14px;
      left: 0;
      width: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: center;

      @media (min-width: 768px) and (max-width: 1024px) {
        bottom: 10px;
      }
    }

    .glide__bullet {
      position: relative;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 13px;
      height: 13px;
      border-radius: 50%;
      box-sizing: border-box;
      padding: 0;
      margin: 0 3px;
      background: #fff;
      box-shadow: 0 0 13px rgba(0, 0, 0, 0.31);
      border: 1px solid #cbcbcb;
      outline: none;

      &--active {
        background: #fff;

        &::before {
          content: '';
          width: 8px;
          height: 8px;
          border-radius: 50%;
          border: 1px solid #cbcbcb;
          background: #fff;
        }
      }

      @media (min-width: 768px) and (max-width: 1024px) {
        width: 9px;
        height: 9px;
      }
    }

    .glide__arrow {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 100px;
      padding: 0 10px;
      background: none;
      border: none;
      outline: none;
      cursor: pointer;

      i {
        color: #fff;
        font-size: 36px;
        text-shadow: 0 0 1px#a8a8a8;
      }
    }

    .glide__arrow--left {
      left: 0;
      text-align: left;
    }

    .glide__arrow--right {
      right: 0;
      text-align: right;
    }
  }

  &__image-item {
    position: relative;

    img {
      width: 100%;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      span {
        width: 22px;
        height: 22px;
      }

      .icon-token {
        font-size: 8px;
      }

      .icon-percent {
        background-size: 65%;
      }

      .icon-percent + .icon-token {
        right: 34px;
      }
    }
  }

  &__order {
    @include width(38.5%);

    flex-shrink: 0;

    .action {
      display: none;
    }

    @media (max-width: 767px) {
      @include width(100%);

      display: flex;
      flex-direction: column;

      .delivery-address {
        display: none;
      }

      .action {
        display: flex;
        margin-top: 0;
        margin-bottom: 16px;
        font-size: 13px;
        order: 10;

        .icon-token {
          width: 32px;
          height: 32px;
          font-size: 10px;
          transform: none;
          background-repeat: no-repeat;
        }
      }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      @include width(calc(44% - 20px));

      .product__description, .features, .delivery-address {
        display: none;
      }

      .action {
        display: flex;
        margin-top: 0;
        margin-bottom: 16px;
        font-size: 13px;

        .icon-token {
          width: 26px;
          height: 24px;
          font-size: 9px;
        }
      }
    }
  }

  &__heading {
    margin-top: 0;
    margin-bottom: 8px;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    color: #404040;
    font-size: 20px;
    font-weight: 400;
    letter-spacing: 0.2px;

    @media (max-width: 767px) {
      margin-top: 22px;
      font-size: 16px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      font-size: 16px;
    }
  }

  &__vendor {
    color: #404040;
    letter-spacing: 0.14px;

    @media (max-width: 767px) {
      font-size: 13px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      font-size: 13px;
    }
  }

  &__description {
    width: 83%;
    margin-top: 0;
    margin-bottom: 23px;
    color: #515151;
    font-size: 16px;

    strong {
      font-weight: 600;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      width: 100%;
      font-size: 13px;
    }
  }

  & + .mobile-features {
    display: none;
  }

  @media (max-width: 767px) {
    flex-wrap: wrap;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    margin-top: 15px;
    margin-bottom: 29px;

    & + .mobile-features {
      display: flex;
      flex-direction: column;
    }
  }
}