.mobile-filters {
  display: none;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 7px 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #c9c9c9;

  &__item {
    position: relative;
    display: inline-flex;
    align-items: center;
    color: #515151;
    font-size: 13px;
    letter-spacing: 0.26px;

    i {
      margin-left: 4px;
      font-size: 9px;
      font-weight: 600;
    }
  }

  &__dropdown {
    position: absolute;
    top: 100%;
    padding: 10px 5px;
    display: inline-flex;
    flex-direction: column;
    z-index: 100;
    height: 0;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    background: #fff;

    &_left {
      left: 0;
    }

    &_right {
      right: 0;
    }

    a {
      margin: 5px 0;
      color: #515151;
      font-size: 13px;
      letter-spacing: 0.26px;
      white-space: nowrap;
    }
  }

  &__item:hover &__dropdown {
    height: auto;
    opacity: 1;
    visibility: visible;
  }

  @media (max-width: 767px) {
      display: flex;
      padding: 7px 0;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
      display: flex;
  }
}