@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?1uw974');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?1uw974#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?1uw974') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?1uw974') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?1uw974##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-ruble-2 {
  &:before {
    content: $icon-ruble-2; 
  }
}
.icon-shopping-cart {
  &:before {
    content: $icon-shopping-cart; 
  }
}
.icon-phone-call {
  &:before {
    content: $icon-phone-call; 
  }
}
.icon-close {
  &:before {
    content: $icon-close; 
  }
}
.icon-next {
  &:before {
    content: $icon-next; 
  }
}
.icon-prev {
  &:before {
    content: $icon-prev; 
  }
}
.icon-search {
  background: none;
  border: none;
  outline: none;
  box-shadow: none;
  font-family: '#{$icomoon-font-family}';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  &:before {
    content: $icon-search; 
  }
}
.icon-user {
  &:before {
    content: $icon-user; 
  }
}
.icon-cart {
  &:before {
    content: $icon-cart; 
  }
}
.icon-check {
  &:before {
    content: $icon-check; 
  }
}

