.payment-form {
    display: flex;
    flex-direction: column;

    &__field {
        position: relative;
        width: 301px;
        margin-bottom: 18px;
        box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
        border: 1px solid #fff;
        background-color: #fff;
        font-size: 16px;
        
        input {
            height: 44px;    
            width: 100%;
            padding: 0 20px;
            line-height: 44px;
            border: none;
            outline: none;
            color: #515151;

            &[type="number"] {
                padding: 0 0 0 20px;
            }
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            width: 20px;
            z-index: 5;
            background-color: #fff;
        }

        @media (max-width: 767px) {
            width: 100%;
            
            input {
                padding: 0 11px;
                height: 32px;
                line-height: 32px;
                font-size: 13px;
            }

            &::before {
                width: 11px;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            width: 220px; 
            padding: 0 15px;
            margin-bottom: 14px;

            input {
                height: 33px;
                line-height: 33px;
                font-size: 13px;
            }

            &::before {
                width: 15px;
            }
        }
    }

    &__count {
        color: #515151;

        @media (max-width: 767px) {
            padding-right: 40%;
            margin-bottom: 12px;
            font-size: 13px;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            font-size: 13px;
        }
    }

    &__link {
        margin-bottom: 26px;
        color: #789d44;

        @media (max-width: 767px) {
            margin-bottom: 21px;
            font-size: 13px;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            margin-bottom: 16px;
            font-size: 13px;
            white-space: nowrap;
        }
    }

    &__checkbox {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 25px;
        color: #515151;
        cursor: pointer;

        span {
            position: relative;
            display: inline-block;
            width: 13px;
            height: 13px;
            margin-right: 9px;
            border-radius: 3px;
            border: 1px solid #515151;

            @media (min-width: 768px) and (max-width: 1024px) {
                width: 11px;
                height: 11px;
            }
        }

        input {
            display: none;
        }

        input + span > i {
            display: none;
            color: #8fb35b;
        }

        input:checked + span > i {
            display: block;
            position: absolute;
            top: calc(50% - 10px);
            left: calc(50% - 4px);

            @media (min-width: 768px) and (max-width: 1024px) {
                top: calc(50% - 7px);
                left: calc(50% - 3px);
                font-size: 10px;   
            }

        }

        @media (max-width: 767px) {
            margin-bottom: 18px;
            font-size: 12px;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            margin-bottom: 16px;
            font-size: 13px;
        }
    }

    &__submit-block {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-start;
        align-items: center;

        .payment-form__link {
            margin-bottom: 0;
        }

        @media (max-width: 767px) {
            flex-wrap: wrap;

            .payment-form__link {
                margin-top: 14px;
            }
        }
    }

    &__button {
        padding: 12px 36px;
        border-radius: 3px;
        border: none;
        outline: none;
        background-color: #8fb35b;
        color: #fff;
        font-size: 16px;
        margin-right: 22px;

        @media (max-width: 767px) {
            padding: 10px 85.5px;
            font-size: 14px;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            padding: 7.5px 20.5px;
            font-size: 13px;
        }
    }

    &__apple {
        display: inline-flex;
        margin-right: 20px;

        img {
            @media (min-width: 768px) and (max-width: 1024px) {
                width: 100%;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            flex-shrink: 0;
            width: 44px;
        }
    }
}