.phone-code {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 62px;
  height: 100%;

  &::before {
    content: attr(data-phone-code);
    position: absolute;
    right: -22px;
    top: 5px;
    color: #515151;
    font-size: 16px;
  }

  &.open {
    bottom: auto;
  }

  &__list {
    height: 0;
    opacity: 0;
    visibility: hidden;
    list-style: none;
    padding: 0;
    margin: 0;
    background: #fff;
    overflow: hidden;
    transition: all ease 0.3s;

    &_open {
      height: auto;
      opacity: 1;
      visibility: visible;
    }
  }

  &__field {
    display: inline-flex;
    align-items: center;
    background: #fff;
    cursor: pointer;

    i {
      margin-top: 5px;
      margin-left: 7px;
      font-size: 12px;
    }
  }

  &__item {
    position: relative;
    display: inline-flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    background: #fff;
    cursor: pointer;
    
    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 3;
    }
  }
}