.gifts-list {
    @include width(76.8421%);

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;

    &__item {
        @include width(calc(25% - 15px));
        flex-shrink: 0;

        @media (max-width: 767px) {
            @include width(calc(50% - 9px/2));
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            @include width(calc(33.3333% - 27px));
        }
    }

    @media (max-width: 767px) {
        @include width(100%);
    }
}