.search-help {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  width: 324px; }
  .search-help button {
    padding: 4.5px 8px;
    margin-right: 10px;
    border: 1px solid #fff;
    border-radius: 3px;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
    outline: none;
    background-color: #fff;
    color: #515151;
    font-size: 16px;
    cursor: pointer; }

.city-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 533px;
  padding: 47px 0 39px 0; }
  .city-modal__heading {
    margin-top: 0;
    margin-bottom: 23px;
    text-align: center;
    color: #515151;
    font-size: 30px;
    font-weight: 400; }
  .city-modal__submit {
    margin-top: 32px;
    padding: 10px 50px;
    border-radius: 3px;
    border: none;
    outline: none;
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    background-color: #8fb35b;
    cursor: pointer; }
  .city-modal__mobile-heading {
    position: relative;
    display: block;
    width: 100%;
    padding: 14px 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.22);
    background-color: #8fb35b;
    color: #fff;
    font-size: 14px;
    text-align: center; }
    .city-modal__mobile-heading .fas {
      position: absolute;
      left: 19px;
      top: calc(50% - 13px/2);
      font-size: 13px; }
  @media (max-width: 767px) {
