.costume-delivery {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
    border-bottom: 1px solid #e1e1e1;
    margin-bottom: 30px;

    &__heading {
        margin-top: 0;
        margin-bottom: 14px;
        font-size: 20px;
        font-weight: 400;
    }

    &__price {
        display: block;
        margin-bottom: 15px;
        font-size: 16px;
    }

    &__conditions {
        margin: 0;
        font-size: 16px;
        word-break: break-word;
    }

    &__image {
        margin-top: 30px;
    }

    @media (max-width: 767px) {
        flex-wrap: wrap;

        &__heading {
            font-size: 14px;
        }
    
        &__price, &__conditions {
            font-size: 13.21px;
        }

        &__wrapper, &__image {
            @include width(100%);
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        flex-wrap: wrap;

        &__heading {
            font-size: 16px;
        }
    
        &__price, &__conditions {
            font-size: 14px;
        }

        &__wrapper, &__image {
            @include width(100%);
        }
    }
}