.catalog-filters {
    float: right;
    position: relative;
    top: -6px;
    z-index: 10;
    margin-right: 34px;

    @media (max-width: 767px) {
        @include width(calc(100% + 18px));
        top: auto;
        float: none;
        padding: 9px 0 7px 0;
        margin-left: -9px;
        margin-right: 0;
        margin-bottom: 18px;
        border-top: 1px solid #dcdcdc;
        border-bottom: 1px solid #dcdcdc;
        background-color: #fdfdfd;
    }
}