.delivery-city {
    margin-right: 18px;

    a {
        display: inline-flex;
        margin-left: 4px;
        color: #515151;
        max-width: 80px;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: 10px;
        white-space: nowrap;
        
        &:hover {
            color: #7e9d51;
        }

        &.nobackdrop {
            display: none;
        }
    
        &:not(.nobackdrop) {
            display: inline-block;
        }

        @media (max-width: 767px) {
            &:not(.nobackdrop) {
                display: none;
            }

            &.nobackdrop {
                display: inline;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            width: 118px;
            display: inline-block;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            line-height: inherit;
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        display: inline-flex; 
    }
}