.drop-list {
  list-style: none;
  padding: 17px 0 0 31px;
  margin: 0;
  overflow: hidden;
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: all ease 0.3s;

  &__item {
    margin-bottom: 22px;
  }

  &__link {
    color: #4f4f4f;
    font-size: 13px;
  }

  &:target, &.active {
    height: 100%;
    visibility: visible;
    opacity: 1;
  }
}