.delivery-photo {
  padding: 10px 0 40px 0;
  
  &__heading {
    margin-top: 0;
    margin-bottom: 12px;
    font-size: 20px;
    font-weight: 400;
  }

  &__text {
      font-size: 16px;
  }

  @media (max-width: 767px) {
    flex-wrap: wrap;

    &__heading {
        font-size: 14px;
    }

    &__text {
        font-size: 13.21px;
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    flex-wrap: wrap;

    &__heading {
        font-size: 16px;
    }

    &__text {
        font-size: 14px;
    }
  }
}