.features {
    padding: 0;
    margin: 23px 0 47px 0;
    list-style: none;

    &__item {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
        margin-bottom: 7px;
        color: #404040;
        letter-spacing: 0.14px;
        cursor: pointer;

        span {
            display: inline-block;
            width: 50px;
        }
    }

    @media (max-width: 767px) {
        margin: 25px 0;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        margin: 0 0 29px 0;
        padding-right: 14px;
        order: 3;

        &__item {
            span {
                width: 29px;
            }
        }
    }
}