.main-carousel {
    margin-bottom: 24px;

    &__item {
        display: block;
        
        img {
            width: 100%;
        }
    }

    .glide__bullets {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 13px;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        z-index: 3;

        @media (max-width: 767px) {
            position: static;
            margin-top: 8px;
            margin-bottom: 14px;
        }
    }

    .glide__bullet {
        display: inline-block;
        margin: 0 3px;
        padding: 0;
        width: 13px;
        height: 13px;
        box-shadow: 0 0 13px rgba(0, 0, 0, 0.31);
        border: 2px solid #ffffff;
        border-radius: 50%;
        outline: none;
        background-color: transparent;
        cursor: pointer;

        @media (max-width: 767px) {
            border: 1px solid #8fb35b;
            box-shadow: none;
        }
    }

    .glide__bullet--active {
        background-color: #fff;

        @media (max-width: 767px) {
            background-color: #8fb35b;
        }
    }

    .glide__arrows {
        display: block;
        height: 0;

        @media (max-width: 767px) {
            display: none;
        }
    }

    .glide__arrow {
        padding: 0 10px;
        position: absolute;
        top: 0;
        bottom: 0;
        background: none;
        border: none;
        outline: none;
        font-size: 36px;
        color: #fff;
        cursor: pointer;

        @media (min-width: 768px) and (max-width: 1024px) {
            padding: 0 20px;
            font-size: 28px;
        }
    }

    .glide__arrow--left {
        left: 0;
    }

    .glide__arrow--right {
        right: 0;
    }

    @media (max-width: 767px) {
        width: calc(100% + 18px);
        margin-left: -9px;

        .category-thumbnails + &  {
            display: none;
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        width: calc(100% + 18px);
        margin-left: -9px;
        margin-bottom: 20px;

        .category-thumbnails + &  {
            display: none;
        }
    }
    
    .category-thumbnails + & {
        margin-bottom: 51px;
        margin-top: -38px;
    }
}