.callback {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 38px 73px 40px 67px;
  width: 767px; }
  .callback__heading {
    margin-top: 0;
    margin-bottom: 14px;
    color: #515151;
    font-size: 25px;
    font-weight: 400; }
  .callback__vendor-code, .callback__description {
    color: #515151;
    font-size: 16px; }
  .callback__description {
    margin-top: 19px;
    margin-bottom: 21px;
    width: 60%;
    white-space: pre-line;
    text-align: center; }
  .callback__field {
    position: relative;
    display: block;
    width: 55.74%;
    margin-bottom: 21px;
    border-radius: 3px;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
    border: 1px solid #fff;
    background-color: #fff; }
    .callback__field input {
      padding: 5.5px 20px;
      border: none;
      outline: none;
      width: 100%;
      font-size: 16px;
      color: #515151; }
      .callback__field input::placeholder {
        color: #c0c0c0;
        font-size: 16px; }
        .callback__field input::placeholder::first-letter {
          color: #515151; }
  .callback__button {
    padding: 10px 46px;
    margin-top: 11px;
    border: none;
    outline: none;
    border-radius: 3px;
    background-color: #8fb35b;
    color: #fff;
    font-size: 14px;
    cursor: pointer; }
