.crop-text {
  position: relative;
  transition: all ease 0.5s;

  &__button {
    padding-left: 30px;
    margin-left: -30px;
    z-index: 3;
    cursor: pointer;
    background:#fff;
    
    &.open {
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }

  span {
    display: block;
  }
}