.seo-text__content {
  position: relative;
  width: 90%;
  flex-basis: 90%;
  color: #515151;
  font-size: 16px;
  line-height: 25.05px;
  overflow: hidden; }
  .seo-text__content .expand-button {
    display: block;
    width: 100%;
    padding-left: 30px;
    margin-left: -30px;
    z-index: 3;
    cursor: pointer;
    background: #fff;
    text-align: right; }
  @media (max-width: 767px) {
    .seo-text__content {
      width: 100%;
      flex-basis: 100%;
      font-size: 13px; } }

.seo-block {
  position: relative;
  margin-bottom: 33px;
  overflow: hidden;
  border-top: 1px solid #d6d6d6;
  border-bottom: 1px solid #d6d6d6;
  transition: all ease 0.3s; }
  .seo-block .expand-button {
    position: absolute;
    bottom: 14px;
    right: 35px;
    color: #7e9d51;
    font-size: 12px;
    text-decoration: underline; }
  @media (max-width: 767px) {
    .seo-block {
      height: 68px;
      margin-bottom: 0; }
      .seo-block .expand-button {
        bottom: 7px;
        right: 15px; } }
  @media (min-width: 768px) and (max-width: 1024px) {
    .seo-block {
      margin-bottom: 0; } }

.fade-enter-active {
  transition: all ease 1s; }

.fade-leave-active {
  transition: all ease 0.5s; }

.fade-enter, .fade-leave-to {
  opacity: 0; }
