.search {
    width: 19.018%;
    flex-basis: 19.018%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: -1px;
    margin-right: 11px;
    border-bottom: 1px solid #4f4f4f;

    &__field {
        height: 25px;
        border: none;
        outline: none;
        font-size: 13px;
        font-weight: 400;
        font-style: italic;
        line-height: 25px;

        &::placeholder {
            color: #515151;
        }
    }

    button {
        font-size: 20px;
        cursor: pointer;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        display: none;
    }
}