.footer-social {
  .bottom-thumbnails {
    @media (max-width: 1024px) {
      align-items: flex-start;
    }
  }

  .bottom-thumbnails li {
    display: inline-flex;
    margin: 0 7px;

    &.apps {
      display: none;

      @media (min-width: 768px) and (max-width: 1024px) {
        flex-shrink: 0;
        display: inline-flex;
        margin: 0 4.5px;

        a {
          width: 108px;
          height: auto;
        }
      }
    }

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      margin: 0 4.5px;
    }
  }

  .bottom-thumbnails a {
    display: inline-block;
    width: 27px;
    height: 27px;

    &:hover {
      img {
        transform: scale(1.2)
      }
    }
  }

  img {
    width: 100%;
    height: 100%;
  }

  @media (max-width: 767px) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 9px;
    margin-top: 19px;
  }
}