.gift-item {
    display: inline-flex;
    flex-direction: column;
    margin-bottom: 29px;

    &__image {
        display: flex;
        align-items: center;
        height: 221px;
        overflow: hidden;
        margin-bottom: 18px;
        box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.1);
        background-color: #fff;
        
        img {
            width: 100%;
        }

        @media (max-width: 767px) {
            img {
                width: 100%;
                height: 100%;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            height: 169px;

            img {
                width: 100%;
                height: 100%;
            }
        }
    }

    &__description {
        width: 100%;
        margin-bottom: 8px;
        color: #515151;
        line-height: 20px;

        @media (max-width: 767px) {
            width: 90%;
            margin: 0 auto 8px auto;
            font-size: 11px;
            text-align: center;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            font-size: 12px;
        }
    }

    &__price {
        display: inline-flex;
        align-items: center;
        font-size: 16px;

        i {
            font-size: 13px;
        }

        @media (max-width: 767px) {
            margin-bottom: 9px;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            font-size: 13px;

            i {
                font-size: 10px;
            }
        }
    }

    &__bottom {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;

        @media (max-width: 767px) {
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    &__button {
        padding: 8px 18.5px;
        border: 1px solid #8fb35b;
        outline: none;
        border-radius: 3px;
        background: transparent;
        box-shadow: none;
        color: #515151;
        cursor: pointer;

        &:hover {
            background: #8fb35b;
            color: #fff;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            padding: 7px 14px;
            font-size: 12px;
        }
    }

    @media (max-width: 767px) {
        margin-bottom: 18px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        margin-bottom: 20px;
    }
}