.category-thumbnails {
  width: calc(100% - 68px);
  margin: auto auto 49px auto;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  .category-list__item-title {
    min-height: 50px;
  }
  
  .category-list__item {
    margin-bottom: 40px;
  }

  @media (max-width: 767px) {
    width: 100%;
    margin: auto auto 25px auto;

    .category-list__item-title {
      min-height: auto;
    }

    .category-list__item {
      margin-bottom: 18px;
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    width: 710px;
    margin: auto auto 29px auto;

    .category-list__item {
      margin-bottom: 30px;
    }
  }
}