.price-value {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    padding-left: 37px;
    margin-bottom: 24px;

    &__item {
        position: relative;
        color: #404040;
        font-size: 30px;
        letter-spacing: 0.3px;
        cursor: pointer;

        input {
            display: none;
        }

        input + span {
            position: absolute;
            top: calc(50% - 15px/2);
            left: -37px;
            width: 15px;
            height: 15px;
            border-radius: 50%;
            border: 1px solid #d6d6d6;
            background: #fff;
        }

        input:checked + span {
            background: radial-gradient(circle at center, #8fb35b 46.6666%, #fff 60%);
        }

        i {
            position: relative;
            top: -2px;
            vertical-align: middle;
            font-size: 20px;
        }

        @media (max-width: 767px) {
            font-size: 25px;

            i {
                font-size: 18px;
            }

            input + span {
                left: -30px;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            font-size: 25px;

            i {
                font-size: 18px;
            }
        }
    }
    
    &__description {
        display: block;
        margin-top: 2px;
        margin-bottom: 22px;
        color: #404040;
        letter-spacing: 0.14px;

        @media (max-width: 767px) {
            margin-bottom: 20px;
            font-size: 13px;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            margin-bottom: 20px;
            font-size: 13px;
        }
    }

    &__photo {
        position: relative;
        display: block;
        margin: 9px 0;
        padding-left: 27px;
        color: #404040;
        letter-spacing: 0.14px;

        &::before {
            content: '';
            position: absolute;
            top: calc(50% - 17px/2);
            left: 0;
            width: 21px;
            height: 17px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url('~@/assets/images/icons/photo-camera.png');
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            font-size: 13px;
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        margin-bottom: 0;
    }
}