.hamburger-menu {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  height: 14px;
  margin-right: 20px;
  cursor: pointer;

  &__layout, &::before, &::after {
    display: block;
    width: 17px;
    height: 2px;
    border-radius: 20px;
    background-color: #515151;
  }

  &::before, &::after {
    content: '';
  }

  @media (max-width: 1024px) {
    display: none;
  }

  @media (max-width: 767px) {
    margin-right: 17px;
    margin-left: 9px;
  }

  @media (max-width: 767px) {
    display: flex;
  }
}