.seo-text {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  padding-left: 80px;
  margin-bottom: 52px;

  &__image {
    height: 100%;
    margin-top: 11px;
    margin-right: 78px;

    @media (max-width: 767px) {
      display: none;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      margin-right: 37px;
    }
  }

  &__heading {
    margin-top: 0;
    margin-bottom: 17px;
    font-weight: 400;
    color: #515151;
    font-size: 20px;
    letter-spacing: 0.4px;

    @media (max-width: 767px) {
      font-size: 14px;
    }
  }

  &__heading + &__image {
    display: none;

    @media (max-width: 767px) {
      display: block;
      width: 123px;
      height: 134px;
      margin:  27px auto;
    }
  }

  @media (max-width: 767px) {
    padding-left: 0;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    padding-left: 29px;
    margin-bottom: 34px;
  }
}