.seo-block {
  position: relative;
  margin-bottom: 33px;
  height: 55px;
  overflow: hidden;
  border-top: 1px solid #d6d6d6;
  border-bottom: 1px solid #d6d6d6;
  transition: all ease 0.3s; }
  .seo-block_open {
    height: auto; }
  .seo-block__list, .seo-block ul {
    padding-left: 28px;
    margin-top: 10px;
    margin-bottom: 7px;
    list-style: none; }
    .seo-block__list-item, .seo-block__list li, .seo-block ul-item, .seo-block ul li {
      position: relative;
      margin-bottom: 8px;
      color: #515151;
      font-size: 12px; }
      .seo-block__list-item:last-child, .seo-block__list li:last-child, .seo-block ul-item:last-child, .seo-block ul li:last-child {
        margin-bottom: 0; }
      .seo-block__list-item::before, .seo-block__list li::before, .seo-block ul-item::before, .seo-block ul li::before {
        content: '';
        position: absolute;
        top: calc(50% - 4px);
        left: -20px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #8fb35b; }
  .seo-block__expand {
    position: absolute;
    bottom: 14px;
    right: 35px;
    color: #7e9d51;
    font-size: 12px;
    text-decoration: underline; }
  @media (max-width: 767px) {
    .seo-block {
      height: 68px;
      margin-bottom: 0; }
      .seo-block__expand {
        bottom: 7px;
        right: 15px; } }
  @media (min-width: 768px) and (max-width: 1024px) {
    .seo-block {
      margin-bottom: 0; } }

.fade-enter-active {
  transition: all ease 1s; }

.fade-leave-active {
  transition: all ease 0.5s; }

.fade-enter, .fade-leave-to {
  opacity: 0; }
