.dropdown-list {
  position: relative;
  list-style: none;
  padding: 20px 0 0 21px;
  margin: 0;

  &__item {
    margin-bottom: 22px;
  }

  &__label {
    position: relative;
    width: 100%;
    display: inline-flex;
    align-items: center;
    color: #4f4f4f;
    font-weight: 600;
    cursor: pointer;

    &::after {
      margin-left: 5px;
      content: "\F107";
      font-family: 'Font Awesome 5 Free';
      font-weight: 900;
      -webkit-font-smoothing: antialiased;
      display: inline-block;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      line-height: 1;
    }

    .circle {
      margin-left: 9px;
    }
    &.active + .drop-list {
      height: 100%;
      visibility: visible;
      opacity: 1;
    }

    &_nodrop {
      &::after {
        display: none;
      }
    }
  }
}