.product-form {
    &__details {
        display: block;
        margin-bottom: 14px;
        color: #404040;
        font-size: 16px;
        letter-spacing: 0.16px;

        strong {
            font-size: 600;
        }

        @media (max-width: 767px) {
            margin-bottom: 9px;
            font-size: 13px;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            font-size: 13px;
        }
    }

    &__buttons {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-top: 27px;
    }

    &__to-cart {
        padding: 12.5px 26px;
        color: #fff;
        background-color: #8fb35b;

        &:hover {
            background-color: #fbc000;
        }
    }

    &__to-cart {
        border-radius: 3px;
        outline: none;
        border: none;
        font-size: 16px;
        cursor: pointer;

        @media (max-width: 767px) {
            font-size: 14px;
        }
    }
}