.type-list {
    display: inline-flex;
    flex-direction: column;
    flex-wrap: nowrap;
    transition: all ease 0.3s;

    &__item {
        margin-bottom: 34px;
        color: #515151;
        font-size: 16px;

        &_active {
            color: #789d44;
        }

        @media (max-width: 767px) {
            display: inline-flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-items: center;
            padding: 5px 19px 5px 5px;
            height: 28px;
            margin-bottom: 15px;
            margin-right: auto;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            margin-bottom: 25px;
            font-size: 14px;
        }
    }

    @media (max-width: 767px) {
        @include width(100%);

        margin-bottom: 19px;
        border-top: 1px solid #c9c9c9;
        border-bottom: 1px solid #c9c9c9;

    }

    &_close {
        height: 28px;
        overflow: hidden;

        .type-list__item {
            display: none;
        }

        .type-list__item_active {
            display: inline-flex;

            position: relative;

            &::before {
                content: "\F107";
                position: absolute;
                top: calc(50% - 18px/2);
                right: 0;
                font-family: 'Font Awesome 5 Free';
                font-weight: 600;
                -webkit-font-smoothing: antialiased;
                display: inline-block;
                font-style: normal;
                font-variant: normal;
                text-rendering: auto;
                color: #000;
            }
        }
    }
}