.full-category {
  display: none;
  justify-content: center;

  a {
    padding: 5.5px 86px;
    border-radius: 3px;
    border: 1px solid #8fb35b;
    color: #515151;
    font-size: 13px;

    &:hover {
      color: #fff;
      background-color: #8fb35b;
    }
  }

  @media (max-width: 767px) {
    display: flex;
  }
}