.price-filter {
    @include row;

    align-items: center;
    margin: 2px -10px 0 -10px;
    
    &__item {
        padding: 0 10px;
    }

    &__value {
        display: inline-flex;
        align-items: center;
        color: #8fb35b;
        line-height: 17px;

        i {
            margin-left: 6px;
            font-size: 9px;
        }

        &_active {
            padding: 5px 12px;
            border-radius: 3px;
            color: #fff;
            background-color: #8fb35b;

            @media (max-width: 767px) {
                padding: 0.5px 2px;
                font-size: 9px;                
            }
        }

        @media (max-width: 767px) {
            font-size: 11px;

            i {
                margin-top: 1px;
                font-size: 6px;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            font-size: 11px;
        }
    }

    @media (max-width: 767px) {
        flex-wrap: wrap;
        width: 100%;
        margin: 0;
    }
}