.header-top-links {
    @include container;
    @include row;

    align-items: center;

    @media (max-width: 767px) {
        position: relative;
        flex-wrap: wrap;
        padding: 0;

        &::before {
            content: '';
            position: absolute;
            top: 45px;
            left: 0;
            right: 0;
            height: 1px;
            background: #d1d1d1;
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        padding: 2.5px 15px;
        margin: auto;
    }
}