.modal-messengers {
    width: 747px;
    padding: 29px 41px 26px 41px;

    &__header {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-around;
        border-bottom: 1px solid #d6d6d6;
    }

    &__text {
        margin: 29px 0 0 0;
        color: #515151;
        font-size: 16px;
    }

    &__list {
        list-style: none;
        padding-left: 46px;
        margin: 0;

        li {
            position: relative;
            margin-bottom: 12px;

            &::before {
                content: '';
                position: absolute;
                top: calc(50% - 7px/2);
                left: -24px;
                width: 7px;
                height: 7px;
                border-radius: 50%;
                background-color: #8fb35b;
            }
        }
    }
}