.breadcrumbs {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-bottom: 12px;

  &__link {
    position: relative;
    margin-right: 24px;
    color: #515151;

    &::before {
      content: "\F054";
      position: absolute;
      right: -12px;
      top: calc(50% - 9px/2);
      font-family: 'Font Awesome 5 Free';
      font-weight: 600;
      -webkit-font-smoothing: antialiased;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      line-height: 1;
      font-size: 9px;
    }

    &:last-child {
      color: #789d44;

      &::before {
        display: none;
      }
    }
  }

  @media (max-width: 767px) {
    display: none;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    display: none;
  }
}