.footer-links {
  @include row;

  width: 100%;
  margin-right: 15px;

  &__column {
    display: inline-flex;
    flex-direction: column;
    flex-wrap: nowrap;
    flex-shrink: 0;
  }

  &__item {
    margin-bottom: 25px;
    color: #515151;

    &:hover {
      color: #7e9d51;
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    display: none;
  }
}