.footer-payments {
  .bottom-heading i {
    font-size: 10px;
    vertical-align: middle;
  }

  .bottom-thumbnails > li {
    display: inline-flex;
    margin: 0 4.5px;

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    @media (max-width: 767px) {
      margin: 0 14px;
    }
  }

  .bottom-thumbnails a:hover {
    img {
      transform: scale(1.2)
    }
  }

  @media (max-width: 767px) {
    overflow: hidden;
    padding: 0 9px;

    .bottom-thumbnails {
      margin-top: 19px;
    }
  }
}