.main-container {
    @include container;

    & + .selected-delivery {
        display: none;

        @media (max-width: 767px) {
            display: flex;
            width: 100%;
            margin-left: 0;
        }
    }

    @media (max-width: 767px) {
        padding: 0 9px;

        .seo-block {
            display: none;
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        padding: 0;

        .seo-block {
            display: none;
        }
    }
}