.payment-methods {
  margin-bottom: 20px;
  
  &__heading {
    margin-top: 48px;
    margin-bottom: 28px;
    color: #515151;
    font-size: 20px;
    font-weight: 400;

    @media (max-width: 767px) {
      margin-top: 19px;
      margin-bottom: 24px;
    }
  }  

  &__wrapper {
    columns: 3;

    @media (max-width: 767px) {
      columns: unset;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &__item {
    display: block;
    margin-bottom: 27px;

    @media (max-width: 767px) {
      @include width(calc(50% - 15px));

      margin-bottom: 20px;
    }
  }

  &__image {
    @media (max-width: 767px) {
      width: 100%;
    }
  }

  &__image-wrapper {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    height: 65px;
  }

  &__description {
    display: block;
    color: #515151;
    font-size: 16px;

    @media (max-width: 767px) {
      font-size: 13px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      font-size: 12px;
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    padding: 0 48px;
  }
}