.banner {
  position: relative;

  &__item {
    display: block;
    position: relative;
    background: #fff;
    cursor: pointer;

    img {
      width: 100%;
    }
  }

  &__button {
    position: absolute;
    bottom: 91px;
    left: calc(50% - 134.5px);
    padding: 11.5px 48px;
    background-image: linear-gradient(to top, #d39399 0%, #f1b9aa 100%);
    color: #fff;
    font-size: 17px;

    @media (max-width: 767px) {
      left: calc(50% - 87.5px);
      bottom: 13px;
      padding: 10px 21.5px;
      font-size: 13px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      bottom: 45px;
    }
  }

  .glide__arrows {
    display: block;
    height: 0;
    z-index: 0;

    @media (max-width: 767px) {
      display: none;
    }
  }

  .glide__arrow {
    position: absolute;
    top: 0;
    bottom: 13px;
    z-index: 3;
    padding: 0 10px;
    background: none;
    border: none;
    outline: none;
    font-size: 36px;
    color: #8fb35b;
    cursor: pointer;

    @media (min-width: 768px) and (max-width: 1024px) {
      font-size: 28px;
    }
  }

  .glide__arrow--left {
    left: -22.5px;

    @media (min-width: 768px) and (max-width: 1024px) {
      left: 22px;
    }
  }

  .glide__arrow--right {
    right: -22.5px;

    @media (min-width: 768px) and (max-width: 1024px) {
      right: 22px;
    }
  }

  .glide__bullets {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 13px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    z-index: 3;

    @media (max-width: 767px) {
      bottom: -20px;
    }
  }

  .glide__bullet {
    display: inline-block;
    margin: 0 3px;
    padding: 0;
    width: 13px;
    height: 13px;
    box-shadow: 0 0 13px rgba(0, 0, 0, 0.1);
    border: 2px solid #8fb35b;
    border-radius: 50%;
    outline: none;
    background-color: transparent;
    cursor: pointer;
  }

  .glide__bullet--active {
    background-color:#8fb35b;
  }

  .glide__track {
    position: relative;
    z-index: 1;
  }

  @media (max-width: 767px) {
    margin-top: 30px;
    margin-bottom: 58px;
  }
}