.category-view {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  visibility: hidden;
  opacity: 0;
  transition: all ease 0.3s;

  &_visible {
    opacity: 1;
    visibility: visible;
  }
}