.user {
    max-width: 19.737%;

    display: inline-flex;
    margin-left: auto;
    padding-right: 34px;
    align-items: center;
    line-height: 18px;
    color: #515151;

    &:hover, &:hover .icon-user {
        color: #7e9d51;
    }

    .icon-user {
        margin-right: 14px;
        font-size: 25px;
        color: #dcdcdc;
    }

    @media (max-width: 767px) {
        margin-left: 0;
        max-width: none;
    }

    @media (max-width: 1024px) {
        display: none;
    }
}