.circle {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    margin-right: 11px;
    color: #fff;
    font-size: 8.72px;
    font-weight: 400;
    background-color: #fbc000;
    transform: scale(1.375)
}