.search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 29px;

  &__field {
    @include width(86.75%);
    padding-left: 32px;
    border: none;
    outline: none;
    border-bottom: 1px solid #4f4f4f;
    color: #515151;
    font-size: 16px;
  }

  &__button {
    padding: 10px 39px;
    border-radius: 3px;
    border: none;
    outline: none;
    box-shadow: none;
    background-color: #fbc000;
    color: #515151;
    font-size: 18px;
    cursor: pointer;
  }
  
  .icon-search {
    position: absolute;
    left: 0;
    top: calc(50% - 8px);
    font-size: 16px;
  }

  @media (max-width: 767px) {
    &__field {
      @include width(79.75%);
    }

    .icon-search {
      top: calc(50% - 12px);
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    &__field {
      @include width(89.75%);
    }
  }

  @media (max-width: 1024px) {
    &__field {
      font-size: 13px;
    }

    &__button {
      padding: 5px 11px;
      font-size: 13px;
    }
  }
}