.title {
  &:hover {
    &::before, &::after {
      z-index: 100;
      opacity: 1;
      visibility: visible;
    }
  }

  &::before, &::after {
    position: absolute;
    z-index: -100;
    opacity: 0;
    visibility: hidden;
    transition: all ease 0.3s;
  }

  &::before {
    content: attr(data-title);
    top: calc(50% - 43px/2);
    left: calc(100% + 15px);
    width: 190px;
    height: 43px;
    display: inline-flex;
    align-items: center;
    padding: 0 32px;
    box-sizing: border-box;
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.41);
    background-color: #fff;
    color: #000;
    font-size: 14px;
    text-align: center;
  }

  &::after {
    content: '';
    top: calc(50% - 7px);
    left: calc(100% + 6px);
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 7px 9px 7px 0;
    border-color: transparent #ffffff transparent transparent;
  }

  &_left {
    &::before {
      right: calc(100% + 15px);
      left: auto;
    }

    &::after {
      right: calc(100% + 6px);
      left: auto;
      transform: scale(-1, 1);
    }
  }
}