.mobile-navigation {
  @include width(0%);

  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 300;
  overflow-y: scroll;
  overflow-x: hidden;
  transition: all ease 0.3s;
  
  &__content {
    @include width(0%);
    border-radius: 3px;
    overflow: hidden;
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.5);
    background-color: #ffffff;
    transition: all ease 0.3s;
  }

  &.active &__content {
    @include width(82.5%);
  }

  &.active {
    @include width(100%);
  }

  .user {
    @include width(100%);

    display: flex;
    padding: 17px 33px 13px 22px;
    border-bottom: 1px solid #c9c9c9;
    font-size: 13px;
  }

  &__status {
    display: block;
    padding: 15px 0 11px 21px;
    margin-bottom: 15px;
    border-top: 1px solid #c9c9c9;
    border-bottom: 1px solid #c9c9c9;
    color: #515151;
    font-size: 13px;
  }

  &__link {
    display: block;
    padding-left: 20px;
    margin-bottom: 20px;
    color: #515151;
    font-size: 13px;

    &:last-child {
      margin-bottom: 14px;
    }

    &:hover {
      color: #7e9d51;
    }
  }

  &__currency {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 15px 15px 21px;
    border-top: 1px solid #c9c9c9;
    color: #515151;
    font-size: 13px;

    i {
      margin-top: 3px;
      font-size: 11px;
    }

    img {
      margin-left: auto;
    }
  }

  @media (max-width: 767px) {
    display: block;
  }
}