.category-image {
  img {
    width: 100%;
  }

  img[alt="milk"] {
    margin-left: -81px;
    margin-top: 88px;
    transform: scale(1.62);
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    img[alt="balloons"] {
      width: 81.5%;
    }

    img[alt="hat"] {
      margin-top: 50px;
      width: 95.4%;
    }

    img[alt="milk"] {
      margin-top: 36px;
      margin-left: -15px;
      transform: scale(1);
    }
  }
}