.fast-order {
  position: absolute;
  top: 0;
  right: 0;
  padding: 4px 15px;
  border-radius: 3px;
  box-shadow: 1px 1px 27px rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  z-index: -10;
  background-color: #8fb35b;
  border: none;
  outline: none;
  color: #fff;
  font-size: 14px;
  line-height: 21px;
  cursor: pointer;
  transition: all ease 0.3s;

  &:hover {
    background-color: #fbc000;
  }

  @media (max-width: 767px) {
    display: none;
  }
}