.category-sorting {
  position: relative;
  margin-left: auto;
  margin-right: 40px;
  padding: 6px 32px 6px 10px;
  border-radius: 3px;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
  background: #fff;
  color: #515151;
  cursor: pointer;

  &::before  {
    content: "\F107";
    position: absolute;
    top: 0;
    right: 0;
    width: 28px;
    height: 28px;
    background: #fff;
    z-index: 10;
    font-family: 'Font Awesome 5 Free';
    font-weight: 600;
    -webkit-font-smoothing: antialiased;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    text-align: center;
    line-height: 28px;
  }

  &__dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 10px 0;
    height: 0;
    opacity: 0;
    overflow: hidden;
    display: inline-flex;
    flex-direction: column;
    z-index: 50;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
    background: #fff;
    transition: all ease 0.3s;
    
    a {
      padding: 6px 32px 6px 10px;
      color: #515151;
      cursor: pointer;
    }
  }

  &:hover &__dropdown {
    height: auto;
    opacity: 1;
  }
}