.pagination {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 43px;

  &__item {
    position: relative;
    display: inline-flex;
    margin: 0 14px;
    color: #789d44;
    font-size: 16px;

    &_active {
      color: #fff;

      &::before {
        content: '';
        position: absolute;
        top: calc(50% - 5px/2);
        left: calc(50% - 5px/2);
        height: 5px;
        width: 5px;
        border-radius: 50%;
        z-index: -1;
        background: #91b45e;
        transform: scale(6);
      }      
    }    
  }

  i {
    font-size: 9px;
    color: #929292;
    font-weight: 600;
  }

  @media (max-width: 767px) {
    margin-bottom: 18px;
  }
}