.payment-currency {
    position: relative;
    display: inline-flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 20px 0 9px;
    cursor: pointer;

    &:hover {
        a, .payment-currency__text {
            z-index: 3;
        }

        .payment-currency__text {
            text-shadow: 0px 0px 1px #515151;
        }

        .payment-currency__dropdown {
            padding: 48px 8px 0 8px;
            height: auto;
            opacity: 1;
            visibility: visible;
        }
    }

    a {
        position: relative;
        margin-left: 8px;
        color: #515151;

        &:hover {
            color: #7e9d51;
        }
    }

    img {
        position: absolute;
        top: 0px;
        right: -33px;
    }

    &__text {
        position: relative;
    }

    &__dropdown {
        width: 100%;
        list-style: none;
        margin: 0;
        padding: 0;
        height: 0;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: -15px;
        left: 0;
        overflow: hidden;
        border: 1px solid #dcdcdc;
        background-color: #ffffff;
        transition: all ease 0.3s;

        a {
            display: block;
            margin: 0 0 15px 0;
            color: #515151;
        }
    }

    @media (max-width: 1024px) {
        display: none;
    }
}