.location-settings {
    @include width(31.755%);

    position: relative;
    padding-left: 23px;

    &::before {
        content: '';
        position: absolute;
        bottom: 0px;
        left: 0;
        width: 14px;
        height: 19px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url('~@/assets/images/icons/pin.png');
    }

    @media (max-width: 767px) {
        @include width(auto);
        padding: 12.5px 10px 12.5px 23px;
        margin: auto;
        order: 10;

        &::before {
            bottom: 50%;
            margin-bottom: -9.5px;
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        @include width(auto);
        margin-right: auto;
    }
}