.delivery-address {
    position: relative;
    display: block;
    padding-left: 25px;
    margin-bottom: 26px;
    color: #404040;
    font-size: 16px;
    letter-spacing: 0.16px;

    &::before {
        content: '';
        position: absolute;
        bottom: 4px;
        left: 0;
        width: 14px;
        height: 19px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url('~@/assets/images/icons/pin.png');
    }
}