.city-modal__field {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  width: 324px;
  height: 30px;
  margin-bottom: 21px;
  border: 1px solid #fff;
  border-radius: 3px;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
  background-color: #fff;
  overflow: hidden; }
  .city-modal__field input {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 21px;
    margin-right: -25px;
    line-height: 30px;
    color: #515151;
    font-size: 16px;
    border: none;
    outline: none;
    z-index: 1; }
  .city-modal__field span {
    position: relative;
    z-index: 3;
    margin-right: 11px;
    color: #4f4f4f;
    font-size: 16px;
    cursor: pointer;
    transition: all ease 0.5s; }
  .city-modal__field_drop {
    overflow: visible; }
  @media (max-width: 767px) {
    .city-modal__field {
      width: calc(100% - 20px);
      height: 44px;
      margin: 31px 0; }
      .city-modal__field input {
        padding-left: 7px;
        font-size: 13px; } }

.city-list {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  list-style: none;
  padding: 7px 11px;
  margin: 0;
  z-index: 0;
  opacity: 0;
  visibility: hidden;
  border-radius: 0 0 3px 3px;
  border: 1px solid #ffffff;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
  background-color: #fff;
  transition: all ease 0.3s; }
  .city-list__item {
    margin-bottom: 9px;
    color: #515151;
    font-size: 16px;
    cursor: pointer; }
  .city-list_opened {
    opacity: 1;
    visibility: visible;
    z-index: 5; }

.up {
  transform: rotate(180deg); }
