.special-price {
    position: relative;
    display: inline-block;
    margin: 31px 0;
    padding-left: 46px;
    color: #515151;

    i {
        position: absolute;
        left: 0;
        top: 0;
        width: 30px;
        height: 30px;
        line-height: 30px;
        border-radius: 50%;
        text-align: center;
        border: 1px solid #fbc000;
        background-color: #fff;
        color: #fbc000;
        font-size: 19px;
        font-weight: 900;
    }

    @media (max-width: 767px) {
        margin: 23px 0;
        font-size: 11px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        margin: 10px 0;
        font-size: 11px;
    }
}