.content {
  &__section {
    width: 67%;
    margin: 0 auto 34px auto;
  }

  &__heading {
    margin: 0 0 28px 0;
    padding: 20px 0 12px 0;
    border-bottom: 1px solid #d6d6d6;
    color: #515151;
    font-size: 30px;
    font-weight: 400;
    letter-spacing: 0.6px;
    text-align: center;
  }

  &__subheading {
    margin-bottom: 24px;
    color: #515151;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
  }

  &__list {
    padding: 0;
    margin: 0 0 35px 0;
    list-style: none;

    li {
      position: relative;
      padding-left: 23px;
      margin-bottom: 20px;
      color: #515151;
      font-size: 16px;
      font-weight: 400;

      &::before {
        content: '';
        position: absolute;
        top: 4px;
        left: 0;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #8fb35b;
      }
    }
  }

  &__selected, &__text {
    color: #515151;
    font-size: 16px;
  }

  &__selected {
    font-weight: 600;
  }

  &__table {
    width: 100%;
    color: #515151;

    td {
      padding: 15px 20px 11px 20px;
    }

    tr {
      td {
        border-bottom: 1px solid #d6d6d6;
        vertical-align: top;
      }

      &:first-child {
        td {
          border-top: 1px solid #d6d6d6;
        }
      }
    }
  }

  @media (max-width: 767px) {
    &__section {
      width: 100%;
      margin-bottom: 24px;
    }
  
    &__heading {
      display: none;
    }
  
    &__subheading {
      margin-bottom: 15px;
      font-size: 11px;
    }
  
    &__list {
      margin: 0 0 21px 0;
  
      li {
        margin-bottom: 11px;
        font-size: 12px;
      }
    }
  
    &__selected, &__text {
      font-size: 13px;
    }
  
    &__table {
      margin: 0 0 0 -9px;
      width: calc(100% + 18px);
      font-size: 12px;
  
      td {
        &:first-child {
          width: 70%;
          padding: 15px 14px 11px 9px;
        }

        &:last-child {
          padding: 15px 9px 11px 14px;
        }
      }
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    &__section {
      width: 100%;
      padding: 0 40px;
      margin: 0 auto 24px auto;
    }
  
    &__heading {
      font-size: 20px;
    }
  
    &__subheading {
      margin-bottom: 15px;
      font-size: 16px;
    }
  
    &__list {
      margin: 0 0 26px 0;
  
      li {
        margin-bottom: 15px;
        font-size: 13px;
      }
    }
  
    &__selected, &__text {
      font-size: 13px;
    }
  
    &__table {
      margin: 0 0 0 -40px;
      width: calc(100% + 80px);
      font-size: 13px;
  
      td {
        padding: 15px 40px 11px 40px;
      }
    }
  }
}