.add-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 800px;
    padding: 40px 30px;

    &__heading {
        margin: 20px 0 0;
        font-size: 24px;
        font-weight: 500;
    }

    &__price {
        margin-top: 10px;
        font-size: 1.15em;
    
        i {
            font-size: 12px;
            vertical-align: middle;
        }
    }

    &__success {
        margin-top: 20px;
        font-size: 1.25em;

        i {
            color: #428300;
        }
    }

    &__bottom {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-top: 20px;
    }

    &__button {
        display: inline-block;
        width: auto;
        height: 35px;
        border-radius: 3px;
        padding: 0 15px;
        margin-right: 10px;
        font-size: 9pt;
        line-height: 32px;
        vertical-align: top;
        border: solid transparent;
        border-width: 1px 1px 2px;
        cursor: pointer;
        color: #fff;
        background-color: #90b25e;
        text-shadow: 1px 1px 1px #464a13;
        
        &.active {
            background-color: #90b25e;
        }

        &:hover {
            background-color: #bfca2b;
        }

        &_grey {
            color: #000;
            background-color: #f0f0f0;
            text-shadow: 1px 1px 1px #d7d7d7;
        }
    }
}