.selected-delivery {
    display: flex;
    justify-content: center;
    padding: 17px 0 5px 0;
    border-top: 1px solid  #d6d6d6;

    &__city {
        position: relative;
        display: inline-block;
        color: #515151;
        font-size: 20px;

        a {
            color: #7e9d51;
        }

        &::before {
            content: '';
            position: absolute;
            bottom: 4px;
            left: -27px;
            width: 17px;
            height: 22px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url('~@/assets/images/icons/pin.png');
        }

        @media (max-width: 1024px) {
            display: none;
        }
    }

    &__link {
        position: relative;
        width: 100%;
        display: none;
        text-align: center;

        i {
            position: absolute;
            top: calc(50% - 13px/2);
            left: 20px;
            font-size: 13px;
        }

        @media (max-width: 1024px) {
            display: block;
        }
    }

    @media (max-width: 1024px) {
        width: calc(100% + 24px*2);
        margin-left: -24px;
        padding: 12px 0 8px 0;
        background-color: #8fb35b;
        font-size: 14px;
        letter-spacing: 0.28px;
        border: none;

        &__link {
            color: #fff;
        }
    }
}