.delivery {
  &__header {
    border-bottom: 1px solid #d6d6d6;
  }

  &__heading, &__subheading {
    color: #515151;
    font-weight: 400;
    text-align: center;
  }

  &__heading {
    margin-top: 40px;
    margin-bottom: 34px;
    font-size: 30px;
    letter-spacing: 0.6px;
  }

  &__subheading {
    margin-top: 0;
    margin-bottom: 19px;
    font-size: 20px;
  }

  &__select {
    padding-top: 49px;
  }
}