.gifts {
    &__heading {
        margin-top: 0;
        margin-bottom: 40px;
        text-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
        color: #404040;
        font-size: 20px;
        font-weight: 400;
        letter-spacing: 0.2px;

        @media (max-width: 767px) {
            width: 60%;
            margin-bottom: 15px;
            font-size: 16px;
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            margin-bottom: 28px;
            font-size: 16px;
        }
    }

    &__thumbnails {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;

        @media (max-width: 767px) {
            flex-wrap: wrap;
        }
    }
}