.modal {
  position: relative;
  border-radius: 3px;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;

  .icon-close {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
  }

  @media (max-width: 767px) {
    width: 100%!important;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    width: 100%!important;
  }
}