.category-list {
  width: calc(100% - 68px);
  margin: auto auto 35px auto;
  
  [data-glide-el="controls"] {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    position: absolute;
    top: 0;
    bottom: 57px;
    left: 0;
    right: 0;
    margin: 0 -22.5px;
    z-index: 0;

    @media (max-width: 767px) {
      display: none;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      bottom: 73px;
    }
  }

  [data-glide-dir] {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 20px;
    background: none;
    border: none;
    outline: none;
    font-size: 36px;
    color: #8fb35b;
    cursor: pointer;

    i {
      margin-bottom: -16px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      font-size: 29px;
    }
  }

  .glide__arrow--left {
    margin-left: -30px;

    @media (min-width: 768px) and (max-width: 1024px) {
      margin-left: -18px;
    }
  }

  .glide__arrow--right {
    margin-right: -30px;

    @media (min-width: 768px) and (max-width: 1024px) {
      margin-right: -18px;
    }
  }

  .glide__track {
    position: relative;
    z-index: 1;
  }

  .glide__slide {
    height: auto;
  }

  &__item {
    position: relative;
    height: 100%;
    padding-bottom: 35px;
    width: calc(33.3333% - 20px);

    &-image {
      position: relative;
      display: flex;
      width: 100%;
      box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.1);
      border-radius: 3px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
      }

      &:before {
        content: '';
      }
      
      &:hover {
        .fast-order {
          visibility: visible;
          opacity: 1;
          z-index: 1;
        }
      }

      @media (min-width: 768px) and (max-width: 1024px) {
        margin-bottom: 19px;
      }
    }

    &-link {
      display: block;
    }

    &-title {
      display: block;
      margin-top: 18px;
      color: #515151;
      font-size: 16px;
      line-height: 25px;
      white-space: pre-line;

      @media (max-width: 767px) {
        display: block;
        width: 100%;
        margin-top: 5px;
        font-size: 11px;
        line-height: 14px;
        text-align: center;
      }

      @media (min-width: 768px) and (max-width: 1024px) {
        font-size: 14px;
      }
    }

    &-price {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      color: #515151;
      font-size: 20px;

      i {
        font-size: 14px;
      }

      @media (max-width: 767px) {
        @include width(100%);

        margin: 9px 0;
        font-size: 14px;
        text-align: center;

        i {
          font-size: 11px;
        }
      }

      @media (min-width: 768px) and (max-width: 1024px) {
        font-size: 18px;
      }
    }

    &-bottom {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
      align-items: center;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;

      @media (max-width: 767px) {
        flex-wrap: wrap;
      }
    }

    @media (max-width: 767px) {
      width: calc(50% - 4.5px);
      padding-bottom: 63px;
      margin-bottom: 18px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      width: calc(33.3333% - 8px);
      padding-bottom: 37px;
    }
  }

  &__carousel {
    @media (max-width: 767px) {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  @media (max-width: 767px) {
    width: 100%;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    width: 710px;
    margin: auto auto 29px auto;
  }
}