.payment-list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0 0 28px 0;

  &__item {
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
    
    img {
      width: 100%;
    }
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    margin: 0 0 24px 0;
  }
}