.icon {
  &-flowers {
    position: absolute;
    left: 11px;
    bottom: 11px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    border: 1px solid #4f7f0d;
    background-color: #fff;
    background-image: url('~@/assets/images/icons/flowers.png');
    background-position: center;
    background-repeat: no-repeat;

    @media (max-width: 767px) {
      left: 5px;
      bottom: 5px;
      transform: scale(0.48) translate(-62%, 62%);
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      left: 7px;
      bottom: 7px;
    }
  }

  &-percent {
    position: absolute;
    right: 11px;
    bottom: 11px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    border: 1px solid #fbc000;
    background-color: #ffffff;
    background-image: url('~@/assets/images/icons/percent.png');
    background-position: center;
    background-repeat: no-repeat;

    @media (max-width: 767px) {
      right: 5px;
      bottom: 5px;
      transform: scale(0.48) translate(62%, 62%);
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      right: 7px;
      bottom: 7px;
    }
  }

  &-like {
    position: absolute;
    left: 11px;
    top: 11px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    border: 1px solid #fb9300;
    background-color: #ffffff;
    background-image: url('~@/assets/images/icons/like.png');
    background-position: center;
    background-repeat: no-repeat;

    @media (max-width: 767px) {
      left: 5px;
      top: 5px;
      transform: scale(0.48) translate(-62%, -62%);
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      left: 7px;
      top: 7px;
    }
  }

  &-token {
    position: absolute;
    right: 11px;
    bottom: 11px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 39px;
    border-radius: 50%;
    box-shadow: 0 0 21px rgba(0, 0, 0, 0.14);
    background-image: url('~@/assets/images/icons/token.png');
    background-position: center;
    background-repeat: no-repeat;
    color: #573c01;
    font-weight: 700;

    @media (max-width: 767px) {
      right: 5px;
      bottom: 5px;
      transform: scale(0.48) translate(62%, 62%);
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      right: 7px;
      bottom: 7px;
    }
  }

  &-percent + &-token {
    right: 55px;

    @media (max-width: 767px) {
      right: 33px;
    }
  }
}