.header {
    position: relative;
    padding-top: 54px;

    &__top {
        display: flex;
        justify-content: center;
        width: 100%;
        background-color: rgba(243,243,243, 0.8);
        position: fixed;
        top: 0;
        left: 0;
        z-index: 100;

        &_scroll {
            background-color: rgba(243,243,243, 1);

            .logo-scroll {
                display: flex;
                justify-content: center;
            }

            .scroll-hidden {
                display: none;
            }

            .location-settings {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: space-around;
                width: 25%;
                flex-basis: 25%;
            }

            .delivery-city {
                margin-right: 0;
            }

            .fast-links {
                width: 25%;
                flex-basis: 25%;
            }

            .fast-links-list {
                margin: auto;
                justify-content: space-around;
            }

            .user {
                @include width(19.5624%);
            }
        }

        @media (max-width: 767px) {
            background-color: rgba(243,243,243, 1);
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            justify-content: space-between;
            background-color: rgba(243,243,243, 1);
        }
    }

    &__middle {
        @include container;
        @include row;

        margin-bottom: 10px;
        align-items: center;

        @media (max-width: 1024px) {
            display: none;
        }
    }

    .seo-block {
        display: none;
    }

    @media (max-width: 767px) {
        padding-top: 87px;

        .seo-block {
            display: block;
        }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
        padding-top: 45px;

        .seo-block {
            display: block;
        }
    }
}