.subcategory {
  width: calc(100% - 68px);
  margin: auto;
  columns: 6;
  column-width: 66px;
  column-gap: 72px;
  margin-bottom: 33px;

  &__item {
    display: block;
    color: #515151;
    font-size: 16px;
    line-height: 25px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  @media (max-width: 767px) {
    display: none;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    display: none;
  }
}